<template>
<body id="poster">
  <el-form class="login-container" label-position="left" label-width="0px">
    <h3 class="login-title">系统登录</h3>
    <el-form-item>
      <el-input
        type="text"
        v-model="loginForm.username"
        auto-complete="off"
        placeholder="账号"
      >
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-input
        type="password"
        v-model="loginForm.password"
        auto-complete="off"
        placeholder="密码"
      >
      </el-input>
    </el-form-item>
    <el-form-item style="width: 100%">
      <el-button
        type="primary"
        style="width: 100%; background: #505458; border: none"
        @click="login"
        >登录
      </el-button>
    </el-form-item>
    <!-- <button v-on:click="login">登录</button> -->
    <h3 v-if="seen">账户或密码错误</h3>
  </el-form>
</body>
</template>



<script>
export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        username: "",
        password: "",
      },
      responseResult: [],
      seen: false,
    };
  },
  methods: {
    login() {
      var _this = this
      console.log(this.$store.state)
      this.$axios
        //   发送数据的代码
        .post("http://localhost:3001/login", {
          username: this.loginForm.username,
          password: this.loginForm.password,
        })
        .then((successResponse) => {
          if (successResponse&&successResponse.status == 200) {
            console.log('@@@2',successResponse);
            // 成功后页面跳转至index主界面
            // this.$router.replace({ path: "/index" });
            _this.$store.commit('login',_this.loginForm)
            var path = this.$route.query.redirect
            this.$router.replace({path: path === '/' || path === undefined ? '/library' : path})
          } else if (successResponse.status == 201) {
            console.log('111111123');
            this.seen = true;
          }
        })
        .catch((failResponse) => {});
    },
  },
  created() {
    window.loginData = this;
  }
};
</script>

<style>
#poster{
  background:url("..\assets\loginbackground.jpeg") no-repeat ;
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}
.login-container{
  border-radius: 15px;
  background-clip: padding-box;
  margin: 90px 900px;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login-title{
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
</style>



